import * as React from 'react'
import { Component } from 'react'
import './App.css'
import { getSettings, setConfig } from '../configuration/configuration'
import Branches from './Branches/Branches'
import { AppBar, Toolbar, Button, Typography } from '@material-ui/core'
import SearchAppBar from './SearchAppBar'
import { Router, Link, RouteComponentProps } from '@reach/router'
import { Settings } from './Settings'

class App extends Component {
    async componentDidMount() {
        const match = window.location.href.match(/\?code=(.*)/)
        if (match) {
            const code = match[1]
            console.log(code)
            setConfig({ githubCode: code })
        }
    }

    componentDidCatch(e: any) {
        console.log(e)
    }

    render() {
        const { githubToken } = getSettings()
        const logged = githubToken && githubToken !== ''
        return (
            <div className="App">
                <SearchAppBar />
                <Router>
                    <Settings
                        path={process.env.PUBLIC_URL + '/configuration'}
                        settings={getSettings()}
                    />
                    <Branches path={process.env.PUBLIC_URL + '/'} />
                    {/* {logged ? (
                    ) : (
                        <Typography variant="h2">Enter Github token</Typography>
                    )} */}
                </Router>
            </div>
        )
    }
}

export default App
